import HTMLReactParser from "html-react-parser";

export const Experience = (props: {
  designation: string,
  company: string,
  location: string,
  join: string,
  exit: string
}) => {
  const { designation, company, location, join, exit } = props;

  return (
    <div>
      {
        designation && (
          <div className="d-flex gap-0-2">
            {
              join && exit && (
                <div>
                  {join}&nbsp;-&nbsp;{exit}
                </div>
              )
            }

            <div className="designation">
              <div className="fw-bold">
                {designation}
              </div>

              {
                company && (
                  <div className="small company">
                    <span className="fw-bold">
                      {HTMLReactParser(company)}
                    </span>

                    {location && (
                      <span className="fw-500 text-muted">
                        &nbsp;-&nbsp;{location}
                      </span>
                    )}
                  </div>
                )
              }
            </div>
          </div>
        )
      }
    </div>
  );
};