import HTMLReactParser from "html-react-parser";
import { nanoid } from "nanoid";


type WorkType = {
  title: string,
  link: string,
  description: string[],
  outcome: string
}


const Work = (props: WorkType) => {
  const { title, link, description, outcome } = props;

  return (
    <div>
      {
        title && (
          <h4>
            {HTMLReactParser(title)}
          </h4>
        )
      }

      {
        link && (
          <a className="text-muted link fw-600" href={link} target="_blank" rel="noopener noreferrer">
            {HTMLReactParser(link)}
          </a>
        )
      }

      {
        description && (
          <ul className="text-muted">
            {
              description.map((item: string) => <li>{HTMLReactParser(item)}</li>)
            }
          </ul>
        )
      }

      {
        outcome && (
          <p className="text-muted">
            {HTMLReactParser(outcome)}
          </p>
        )
      }
    </div>
  );
};


const AddWork = (props: {
  work: WorkType[]
}) => {
  const { work } = props;

  return (
    <>
      {
        work.map((data) => {
          return (
            <Work
              key={nanoid()}
              title={data.title}
              link={data.link}
              description={data.description}
              outcome={data.outcome}
            />
          );
        })
      }
    </>
  );
};


export { Work, AddWork };